<template>
  <div class="main-screen">
    <div class="main-screen-wrapper">
      <div class="main-screen__main">
        <img
          src="@/assets/logo.svg"
          alt="aiscreen"
          class="main-screen-main__logo"
        />
        <h2 class="main-screen-main__title">
          Welcome to AIScreen Mykita Shcherbina!
        </h2>
        <p class="main-screen-main__text">
          Show your content on screen or in demo mode in 60 seconds. Get started
          quickly and easily.
        </p>
        <form
          action="#"
          :class="[
            'main-screen-main__form',
            { 'state-active': selectedCountry === 'USA' },
          ]"
        >
          <div class="main-screen__form-item">
            <input
              type="text"
              class="main-screen__form-input"
              placeholder="Input text"
              required
            />
          </div>
          <div class="main-screen__form-item">
            <select
              class="main-screen__form-select main-screen__form-select_industry"
              required
              @change="changeIndustry"
            >
              <option value="plug" disabled selected>Industry</option>
              <option
                v-for="industry in industries"
                :key="industry"
                :value="industry"
              >
                {{ industry }}
              </option>
            </select>
          </div>
          <div class="main-screen__form-item">
            <select
              class="main-screen__form-select"
              required
              v-model="selectedCountry"
            >
              <option value="" disabled selected>Country</option>
              <option value="USA">USA</option>
              <option value="NotUSA">Not USA</option>
            </select>
          </div>
          <div class="main-screen__form-item" v-if="selectedCountry === 'USA'">
            <select class="main-screen__form-select" required>
              <option value="" disabled selected>State</option>
              <option value="1">1</option>
              <option value="2">2</option>
            </select>
          </div>
          <button
            class="main-screen__form-btn hover-btn"
            @click.prevent="nextScreen"
          >
            Next step
          </button>
        </form>
      </div>
      <div class="main-screen__img">
        <div class="backgorund">
          <img
            v-if="industry"
            :src="require(`@/assets/${industry}.png`)"
            alt="img"
            class="backgorund__img"
          />
        </div>
        <img
          :src="require(`@/assets/${industry}-small.png`)"
          alt="img"
          v-if="industry"
          class="main-screen-main__small-img"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MainScreen',
  data() {
    return {
      selectedCountry: '',
      industry: '',
      industries: [
        // 'Energy',
        // 'Events',
        // 'Education',
        'Finance',
        // 'Fitness',
        // 'Healthcare',
        // 'Hospitality/Food Beverage',
        // 'Manufacturing',
        // 'Media & Advertising',
        // 'Places Of Worship',
        // 'Real Estate',
        'Retail',
        // 'Software & Services',
        // 'Telecommunication',
        // 'Other',
      ],
    }
  },
  computed: {},
  methods: {
    nextScreen() {
      const form = this.$el.querySelector('form')
      const requiredFields = form.querySelectorAll('[required]')
      let isValid = true

      requiredFields.forEach((field) => {
        console.log(field.value)
        if (field.value == 'plug' || !field.value) {
          isValid = false
          field.classList.add('warning') // Add a CSS class for styling
        } else {
          field.classList.remove('warning') // Remove the CSS class if field is filled
        }
      })

      if (isValid) {
        this.$emit('nextScreen')
        console.log('nextScreen')
      } else {
        console.log('Please fill in all required fields')
      }
    },
    changeIndustry() {
      const selectElement = this.$el.querySelector(
        '.main-screen__form-select_industry'
      )
      const img = this.$el.querySelector('.main-screen-main__small-img')
      const bigImg = this.$el.querySelector('.main-screen-main__small-img')

      if (this.industry) {
        if (img && bigImg) {
          img.style.transition = 'all 0.2s ease-out'
          img.style.opacity = 0
          bigImg.style.transition = 'all 0.2s ease-out'
          bigImg.style.opacity = 0
          console.log('change industry')
        }
      }

      setTimeout(() => {
        const selectedOption =
          selectElement?.querySelector('option:checked') ||
          selectElement?.querySelector('option[value="plug"]')

        this.industry = selectedOption ? selectedOption.value : ''
        this.$emit('changeIndustry', this.industry)
        console.log(this.industry)
      }, 200)

      if (this.industry) {
        if (img && bigImg) {
          setTimeout(() => {
            img.style.opacity = 1
            img.style.transition = 'all 0.2s ease-out'
            setTimeout(() => {
              bigImg.style.opacity = 1
              bigImg.style.transition = 'all 0.2s ease-out'
            }, 300)
          }, 300)
        }
      }
    },
  },
  mounted() {
    this.changeIndustry()
  },
}
</script>

<style lang="scss" scoped>
.main-screen {
  max-width: 1600px;
  background: #fff;
  border-radius: 30px;
  margin: 0 auto;
  padding: 13px 13px 14px 80px;
  &-wrapper {
    display: flex;
    flex-direction: row;
    gap: 90px;
  }
  &__main {
    display: flex;
    max-width: 426px;
    flex-direction: column;
    .main-screen-main__logo {
      margin-bottom: 111px;
      max-width: 128px;
      padding-top: 27px;
    }
    .main-screen-main__title {
      line-height: 48px;
      font-size: 40px;
      margin-bottom: 17px;
    }
    .main-screen-main__text {
      line-height: 21px;
      font-size: 17px;
      margin-bottom: 55px;
      font-weight: 700;
      color: #86868b;
    }
    .main-screen-main__form {
      display: flex;
      flex-direction: column;
      gap: 17px;
      padding-bottom: 170px;
      .warning {
        border: 2px solid red !important;
      }
      &.state-active {
        padding-bottom: 100px;
      }
      .main-screen__form-btn {
        margin-top: 14px;
        width: 100%;
        text-align: center;
        display: unset;
        padding: 17px 0;
      }
      .main-screen__form-item {
        .main-screen__form-input {
          background: #fff;
          border-radius: 13px;
          padding: 15px;
          width: 100%;
          font-weight: 500;
          font-size: 17px;
          line-height: 21px;
          color: #86868b;
          border: 1px solid #86868b80;
          &::placeholder {
            color: #86868b;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
          }
        }
        .main-screen__form-select {
          background: #fff;
          border-radius: 13px;
          padding: 15px;
          width: 100%;
          font-weight: 500;
          font-size: 17px;
          line-height: 21px;
          color: #86868b;
          border: 1px solid #86868b80;
          &::placeholder {
            color: #86868b;
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
          }
        }
      }
    }
  }
  &__img {
    width: 100%;
    position: relative;
    .hide {
      opacity: 0;
      transition: all 0.5s ease;
    }
    .show {
      opacity: 1;
      transition: all 0.5s ease;
    }
    .backgorund {
      img {
        width: 100%;
        height: 100%;
      }
      width: 100%;
      height: 100%;
      border-radius: 17px;
    }
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 75%;
      height: 50%;
      &::after {
        content: '';
        width: 110%;
        height: 110%;
        background: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 17px;
      }
    }
  }
}
</style>
